function emit(eventName, detail) {
  var event;
  if (window.CustomEvent) {
    console.log('creating window ' + eventName);
    event = new CustomEvent(eventName, { detail });
  } else {
    event = document.createEvent('CustomEvent');
    event.initCustomEvent(eventName, true, true, { detail });
  }
  window.dispatchEvent(event);
}

export const registerWithMobileRuntime = (muiletId) => {
  window.onLaunch = (detail) => {
    console.log('onLaunch was triggered by mobile');
    emit('onLaunchEvent', detail);
  };

  window.onShow = (detail) => {
    console.log('onShow was triggered by mobile');
    emit('onShowEvent', detail);
  };

  window.onHide = (detail) => {
    console.log('onHide was triggered by mobile');
    emit('onHideEvent', detail);
  };

  window.onDeeplink = (detail) => {
    console.log('onDeeplink was triggered by mobile');
    emit('onDeeplinkEvent', detail);
  };

  const reqData = {
    muiletId,
    metadata: {
      version: 1.0,
    },
  };
  const jsonData = JSON.stringify(reqData);
  setTimeout(() => {
    console.log('window.MuiABJSBridge after render...', window.MuiABJSBridge?.registerLifeCycleCallback);
    if (typeof window.MuiABJSBridge?.registerLifeCycleCallback === 'function') {
      console.log('calling registerLifeCycleCallback...');
      window.MuiABJSBridge.registerLifeCycleCallback(jsonData);      
    }
  }, 400);
};

export const isMobile = () => {
  if (window.MuiABJSBridge || window.location.href.startsWith('file://')) {
    return true;
  }
};
