import React from 'react';
import { Grid, Card, Typography, TextField, IconButton, MenuItem, Menu, ButtonGroup, Button  } from 'material-ui';
import male from '../assets/top-bar/male.png';
import female from '../assets/top-bar/female.png';
import joint from '../assets/top-bar/joint.png';
import { createTheme, ThemeProvider } from 'material-ui';
import Emitter from '../helpers/event.emitter';
import consts from '../helpers/constants';
import flag from './flags';

// To fix to top
// position: fixed;
// height: 85px;
// margin-top: 15px;
// background: white;
// width: calc('100%' - 200px);

const themeProvider = createTheme({
  components: {
    MuiTextField: {
      styleOverrides: {
        root: {
          background: '#EDF2F7',
          borderRadius: 20,
          '& .MuiOutlinedInput-root': {
            borderRadius: 20,
            '& fieldset': {
              borderColor: '#EDF2F7',
            },
            '&:hover fieldset': {
              borderColor: '#555',
            },
            '&.Mui-focused fieldset': {
              borderColor: 'white',
            },
          },
        },
      },
    },
  },
});

export default function Root(props) {
  const profile = props.profile;

  const [country, setCountry] = React.useState(profile.defaultCountry)
  
  const search = <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 512 512" height="23px" width="23px"><path fill="#eee" d="M505 442.7L405.3 343c-4.5-4.5-10.6-7-17-7H372c27.6-35.3 44-79.7 44-128C416 93.1 322.9 0 208 0S0 93.1 0 208s93.1 208 208 208c48.3 0 92.7-16.4 128-44v16.3c0 6.4 2.5 12.5 7 17l99.7 99.7c9.4 9.4 24.6 9.4 33.9 0l28.3-28.3c9.4-9.4 9.4-24.6.1-34zM208 336c-70.7 0-128-57.2-128-128 0-70.7 57.2-128 128-128 70.7 0 128 57.2 128 128 0 70.7-57.2 128-128 128z"/></svg>
  const chat = <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 576 512" height="25px" width="25px"><path fill="#eee" d="M416 192c0-88.4-93.1-160-208-160S0 103.6 0 192c0 34.3 14.1 65.9 38 92-13.4 30.2-35.5 54.2-35.8 54.5-2.2 2.3-2.8 5.7-1.5 8.7S4.8 352 8 352c36.6 0 66.9-12.3 88.7-25 32.2 15.7 70.3 25 111.3 25 114.9 0 208-71.6 208-160zm122 220c23.9-26 38-57.7 38-92 0-66.9-53.5-124.2-129.3-148.1.9 6.6 1.3 13.3 1.3 20.1 0 105.9-107.7 192-240 192-10.8 0-21.3-.8-31.7-1.9C207.8 439.6 281.8 480 368 480c41 0 79.1-9.2 111.3-25 21.8 12.7 52.1 25 88.7 25 3.2 0 6.1-1.9 7.3-4.8 1.3-2.9.7-6.3-1.5-8.7-.3-.3-22.4-24.2-35.8-54.5z"/></svg>
  const arrow = <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 320 512" height="25px" width="25px"><path d="M143 352.3L7 216.3c-9.4-9.4-9.4-24.6 0-33.9l22.6-22.6c9.4-9.4 24.6-9.4 33.9 0l96.4 96.4 96.4-96.4c9.4-9.4 24.6-9.4 33.9 0l22.6 22.6c9.4 9.4 9.4 24.6 0 33.9l-136 136c-9.2 9.4-24.4 9.4-33.8 0z"/></svg>

  const [anchorEl, setAnchorEl] = React.useState(null);
  const open = Boolean(anchorEl);
  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = (e, selectedCountry) => {
    setAnchorEl(null);
    if (!selectedCountry) return;
    if (country === selectedCountry) return;
    Emitter.emit(consts.RUNTIME_VAR_CHANGED, consts.PUBLIC, { country: selectedCountry })
    setCountry(selectedCountry)
  };

  return (<ThemeProvider theme={themeProvider}>
    <Grid container style={{height: 85}}>
      <Grid md={4} style={{ padding: 0, paddingRight: 10, height: 70 }}>
        <Card elevation={2} style={{border: '1px solid #e4e4e4', height: 70}}>
          <Grid container justifyContent="center">
            <Grid item>
              <img src={profile.type === 'female' ? female : profile.type === 'joint' ? joint : male} alt="image" style={{height: 70}} />
            </Grid>
            <Grid item alignSelf='center' style={{paddingLeft: 15}}>
              <Typography style={{color: '#999'}} >Welcome</Typography>
              <Typography>{profile?.name}</Typography>
            </Grid>
          </Grid>
        </Card>
      </Grid>
      <Grid md={8} style={{ height: 70, paddingLeft: 5 }}>
        <Card elevation={2} style={{border: '1px solid #e4e4e4', height: 70, paddingLeft: 10, paddingRight: 10}}>
          <Grid container justifyContent="space-between" >
            <Grid container md={8} justifyContent="flex-start" alignItems="center">
              <Grid item md={8} style={{padding: 5}}>
                <TextField fullWidth placeholder="Search here"/>
              </Grid>
              <Grid item style={{background: '#005093', borderRadius: 25, height: 44, width: 44}}>
                <IconButton>
                  {search}
                </IconButton>
              </Grid>
            </Grid>
            <Grid container md={4} justifyContent="flex-end" alignItems="center" style={{padding: 5}}>
              <Grid item style={{background: '#005093', marginRight: 15, borderRadius: 25, height: 44, width: 44}}>
                <IconButton>
                  {chat}
                </IconButton>
              </Grid>
              <Grid item >
                <ButtonGroup>
                  <Button style={{paddingBottom: 5, paddingTop: 5, borderRight: 0}}
                    size="small" onClick={handleClick}
                  >
                    {flag(country)}
                  </Button>
                  <Button style={{paddingBottom: 5, paddingTop: 5, borderLeft: 0}}
                    size="small" onClick={handleClick}
                  >
                    {arrow}
                  </Button>
                </ButtonGroup>
                <Menu anchorEl={anchorEl} open={open} onClose={handleClose} >
                  {profile.allowedCountries?.map(c => <MenuItem onClick={(e) => handleClose(e, c)} disableRipple>
                    {flag(c)} &nbsp; {c}
                  </MenuItem>)}
                </Menu>
              </Grid>
            </Grid>
          </Grid>
        </Card>
      </Grid>
    </Grid>
  </ThemeProvider>);
}
