import { useEffect, useState } from 'react'
import { isMobile, registerWithMobileRuntime } from '../helpers/register.mobile'

const useBridge = (mfId) => {
	const [launchOptions, setLaunchOptions] = useState({});

	useEffect(() => {
		if (isMobile()) {
		  window.addEventListener('onLaunchEvent', ({ detail }) => setLaunchOptions(detail));
		  registerWithMobileRuntime(mfId)
		  return () => {
			window.removeEventListener('onLaunchEvent', setLaunchOptions);
		  };        
		}
	}, []);
	return [launchOptions];
};

export default useBridge;
